.social {
    margin-top: -8px;
    margin-right: 30px;
  
    @include vp-1023 {
      margin-top: 0;
      margin-right: 0;
      display: flex;
      justify-content: space-between;
      margin-bottom: 36px;
    }
  
    @include vp-767 {
      flex-direction: column;
      padding-top: 35px;
      padding-bottom: 23px;
      margin-bottom: 0;
      align-self: center;
      justify-content: center;
      width: 100%;
    }
  }
  
  .social__list {
    padding: 0;
    margin: 0;
    list-style-type: none;
    display: grid;
    grid-template-columns: 40px 40px 40px;
    column-gap: 15px;
    row-gap: 10px;
    margin-left: 10px;
  
    @include vp-1023 {
      margin-left: 0;
      margin-right: -5px;
    }
  
    @include vp-767 {
      margin: 0 auto;
    }
  }
  
  .social__link {
    display: block;
    padding: 5px;
  
    &:hover svg {
      fill: $color-default-blue;
    }
  }
  
  .social__link svg {
    display: block;
    fill: #f8fdff;
  }