.contacts {
    display: flex;
    flex-direction: column;
    font-style: normal;
    opacity: 0.7;
  
    @include vp-767 {
      padding-bottom: 8px;
    }
  }
  
  .contacts__street {
    line-height: 22px;
    margin-top: 0;
    margin-bottom: 8px;
  
    @include vp-767 {
      font-size: 14px;
      line-height: 19px;
      margin-bottom: 16px;
    }
  }
  
  .contacts__phone {
    display: inline-flex;
    align-self: start;
    line-height: 25px;
    color: $color-default-white;
  
    &:not(:last-child) {
      margin-bottom: 4px;
    }
  
    @include vp-767 {
      font-size: 14px;
      line-height: 25px;
  
      &:not(:last-child) {
        margin-bottom: 6px;
      }
    }
  }