.feedback {
    position: relative;
    padding-top: 8.5%;
    padding-bottom: 13%;
    background-color: $color-default-blue;
    background-image: url("../img/svg/triangle.svg");
    background-repeat: no-repeat;
    background-size: 50.5% auto;
  
    @include vp-1023 {
      background: none;
      background-color: $color-default-blue;
      padding-top: 47px;
      padding-bottom: 124px;
    }
  
    @include vp-767 {
      padding-bottom: 90px;
    }
  }
  
  .feedback__inner {
    display: grid;
    grid-template-columns: auto 580px;
    column-gap: 120px;
    padding-right: 60px;
  
    img {
      align-self: center;
      height: fit-content;
      position: relative;
      bottom: -15px;
      left: 5px;
    }
  
    @include vp-1023 {
      grid-template-columns: none;
      padding-right: 50px;
  
      img {
        display: none;
      }
    }
  }
  
  .feedback__form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 30px;
    grid-template-rows: auto auto auto auto auto;
  
    @include vp-1023 {
      width: auto;
    }

    @include vp-767 {
      display: flex;
      flex-direction: column;
    }
  }

  .feedback__form h2 {
    grid-column: 1/-1;
    grid-row: 1/2;
    font-family: $ff-roboto;
    font-weight: 400;
    font-size: 36px;
    line-height: 51px;
    color: $color-white;
    margin-top: 0;
    margin-bottom: 10px;
    margin-left: -5px;
  
    @include vp-1023 {
      margin-bottom: 8px;
      margin-left: 0;
    }
  
    @include vp-767 {
      font-size: 30px;
      line-height: 42px;
    }
  }
  
  .feedback__form p {
    grid-column: 1/-1;
    grid-row: 2/3;
    font-size: 16px;
    line-height: 22px;
    color: $color-white;
    margin-top: 0;
    margin-bottom: 35px;
  
    @include vp-1023 {
      margin-bottom: 22px;
    }
  
    @include vp-767 {
      font-size: 14px;
      line-height: 19px;
      margin-bottom: 32px;
    }
  }
  
  .feedback__form input {
    grid-row: 3/4;
    border: none;
    background-color: rgba(4, 20, 39, 0.15);
    border-radius: 3px;
    padding: 15px;
    color: $color-white;
    font-size: 16px;
    line-height: 22px;
    margin-bottom: 18px;
  
    &::placeholder {
      color: $color-white;
    }
  
    &:hover,
    &:focus {
      outline: 1px solid $color-default-black;
    }
  }
  
  .feedback__form textarea {
    grid-column: 1/-1;
    grid-row: 4/5;
    border: none;
    background-color: rgba(4, 20, 39, 0.15);
    border-radius: 3px;
    padding: 20px 15px;
    color: $color-white;
    font-size: 16px;
    line-height: 22px;
    margin-bottom: 35px;
    height: 155px;
  
    &::placeholder {
      color: $color-white;
    }
  
    &:hover,
    &:focus {
      outline: 1px solid $color-default-black;
    }
  
    @include vp-1023 {
      padding: 32px 15px;
      margin-bottom: 37px;
    }
  
    @include vp-767 {
      height: 180px;
      margin-bottom: 30px;
      padding: 24px 15px;
    }
  }
  
  .feedback__form button {
    grid-column: 1/2;
    grid-row: 5/6;
    font-family: $ff-roboto;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: $color-white;
    background-color: $color-default-black;
    border: 0;
    border-radius: 3px;
    padding: 15px 25px 11px 25px;
    cursor: pointer;
    width: 265px;
    user-select: none;
  
    &:hover {
      background-color: rgba(4, 24, 39, 0.7);
    }
  
    @include vp-767 {
      width: 100%;
      margin-bottom: 25px;
    }
  }
  
  .feedback__form label {
    grid-column: 2/3;
    grid-row: 5/6;
    display: flex;
    align-items: center;
    margin-top: 14px;
    margin-left: 8px;
    cursor: pointer;
  
    @include vp-1023 {
      margin-left: 0;
      margin-top: 5px;
    }
  
    @include vp-767 {
      margin-left: 5px;
      order: 1;
    }
  }
  
  .feedback__form span:last-of-type {
    font-size: 14px;
    line-height: 16px;
    color: $color-white;
    opacity: 0.8;
  }
  
  .feedback__checkbox-mark {
    display: block;
    width: 20px;
    height: 15px;
    border: 1px solid $color-white;
    margin-right: 11px;
    position: relative;
    border-radius: 2px;
  
    @include vp-1023 {
      width: 15px;
      height: 15px;
    }
  
    @include vp-767 {
      width: 15px;
      height: 15px;
    }
  }
  
  
  .feedback__form input[type="checkbox"]:checked ~ .feedback__checkbox-mark::before {
    position: absolute;
    content: "";
    left: 2px;
    top: 7px;
    width: 4px;
    height: 1px;
    background: $color-white;
    transform: rotate(50deg);
  }
  
  .feedback__form input[type="checkbox"]:checked ~ .feedback__checkbox-mark::after {
    content: "";
    position: absolute;
    left: 4px;
    top: 6px;
    width: 7px;
    height: 1px;
    background: $color-white;
    transform: rotate(-50deg);
  }
  
  .feedback__form input[type="checkbox"]:hover ~ span {
    color: $color-default-black;
  }
  
  .feedback__form input[type="checkbox"]:hover + .feedback__checkbox-mark,
  .feedback__form input[type="checkbox"]:focus + .feedback__checkbox-mark {
    border-color: $color-default-black;
  }
  
  .feedback__form input[type="checkbox"]:checked:hover ~ .feedback__checkbox-mark::before,
  .feedback__form input[type="checkbox"]:checked:hover ~ .feedback__checkbox-mark::after,
  .feedback__form input[type="checkbox"]:checked:focus ~ .feedback__checkbox-mark::before,
  .feedback__form input[type="checkbox"]:checked:focus ~ .feedback__checkbox-mark::after {
    background-color: $color-default-black;
  }